// 引入 VueRouter
import VueRouter from "vue-router";



export default new VueRouter({
    routes: [
        {
            path:'/',
            name:'home',
            // redirect  重定向,默认为重定向路由
            redirect:'/video',
        },
        {
            path:'/video',
            name:'video',
            component:()=> import("../components/articleContent/videoContent.vue")
        },
        {
            path:'/game',
            name:'game',
            component:()=> import("../components/articleContent/gameContent.vue"),
            // children:[
            //     {
            //         path:'/gameList',
            //         component:gameContent
            //     }
            // ]
        },
        {
            path:'/study',
            name:'study',
            component:()=> import("../components/articleContent/studyContent.vue")
        },
        {
            path:'/app',
            name:'app',
            component:()=> import("../components/articleContent/appContent.vue")
        },
        {
            path:'/netdisk',
            name:'netdisk',
            component:()=> import("../components/articleContent/netdiskContent.vue")
        },
        {
            path:'/book',
            name:'book',
            component:()=> import("../components/articleContent/bookContent.vue")
        },
        {
            path:'/script',
            name:'script',
            component:()=> import("../components/articleContent/scriptContent.vue")
        },
    ]
})