// 引入Vue核心库
import Vue from "vue";
// 引入Vuex
import Vuex from "vuex";
// 应用Vuex插件
Vue.use(Vuex);

// 引入清单
import video from "./List/video"
import game from "./List/game"
import study from "./List/study"
import app from "./List/app"
import book from "./List/book"
import netdisk from "./List/netdisk"
import script from "./List/script"

const gameContent = {
  namespaced: true, //开启命名空间
  // 准备action对象 —————响应组件中用户的动作
  actions: {},
  // 准备mutation对象 ————修改state中的数据
  mutations: {},
  // 准备 state对象 ————保存具体的数据
  state: {
    // 游戏清单
    gameList: game,
  },
};
const videoContent = {
  namespaced: true,
  state: {
    // 影视清单
    videoList: video
  },
};
const studyContent = {
  namespaced: true,
  state: {
    studyList: study
  },
};
const appContent = {
  namespaced: true,
  state: {
    appList: app
  },
};
const bookContent = {
  namespaced: true,
  state: {
    // 书籍
    bookList: book
  },
};
const netdiskContent = {
  namespaced: true,
  state: {
    // 网盘检索
    netdiskList: netdisk
  },
};

const scriptContent = {
  namespaced: true,
  state: {
    scriptList: script
  },
};




// 创建并暴露store
export default new Vuex.Store({
  modules: {
    gameContent,
    videoContent,
    studyContent,
    appContent,
    bookContent,
    netdiskContent,
    scriptContent
  },
});
